.someTest {
  .quiz {
      width: 100%;
      text-align: center;
        h1 {
          padding-top: 100px;
          font-size: 60px;
        }
        .allAnswer {
          padding-top: 50px;
          max-width: 1000px;
          margin: 0 auto;
          display: flex;
          align-items: center;
          justify-content: space-around;
          flex-wrap: wrap;
          p {
            cursor: pointer;
            display: block;
            width: 230px;
            height: 40px;
            font-size: 24px;
            color: #000;
            transition: 0.5s;
            border-radius: 6px;
              &:hover {
                color: #fff;
                background-color: #000;
              }
          }
        }
  }
  .allCorrectAnswer {
    width: 100%;
    text-align: center;
      h3 {
        font-size: 30px;
        padding-top: 50px;
          span {
            font-size: 40px;
            color: chocolate;
          }
      }
      button {
        cursor: pointer;
        font-size: 20px;
        width: 230px;
        height: 40px;
        color: #000;
        background-color: #fff;
        border: 1px solid #000;
        transition: 0.5s;
        font-size: 500;
        border-radius: 6px;
              &:hover {
                color: #fff;
                background-color: #000;
              }
      }
  }
}
.welcome {
  width: 100%;
  text-align: center;
    h1 {
      padding-top: 100px;
      font-size: 60px;
    }
    p {
        font-size: 24px;
        color: #000;
    }
    button {
      cursor: pointer;
      display: inline-block;
      width: 230px;
      height: 40px;
      font-size: 24px;
      color: #000;
      background-color: #fff;
      border: 1px solid #000;
      transition: 0.5s;
      border-radius: 6px;
        &:hover {
          color: #fff;
          background-color: #000;
        }
    }
}